import React from "react";
import { Form, InputNumber, Button, Switch } from "antd";
import { useSelector, useDispatch } from "react-redux";
import { modalClose } from "../../store/actions/modal";
import { createSale, createOrder } from "../../store/actions/api";

const NewSell = () => {
  const payload = useSelector((state) => state.modal.payload);
  const dataList = useSelector((state) => state.api.dataList);
  const token = useSelector((state) => state.auth.token);
  const [quantity, setQuantity] = React.useState(null);
  const dispatch = useDispatch();

  const handleSubmit = (values) => {
    const order = values.switch;
    delete values.switch;
    const formatedValues = {
      ...values,
      article: payload.id,
    };
    dispatch(createSale(formatedValues, token));
    if (order) {
      dispatch(
        createOrder(
          {
            article: payload.id,
          },
          token
        )
      );
    }
    dispatch(modalClose());
  };

  return (
    <>
      <h2>Vender articulo</h2>
      {dataList.results[payload.key] ? (
        <>
          <p>Estas por vender: {dataList.results[payload.key].name}</p>
          <p>Stock disponible: {dataList.results[payload.key].quantity}</p>
          <p>Costo de compra: {dataList.results[payload.key].cost}</p>
          <p>
            Precio sugerido: {dataList.results[payload.key].suggested_price}
          </p>
        </>
      ) : null}
      <Form onFinish={handleSubmit} layout="vertical">
        <Form.Item
          label="Cantidad"
          name="quantity"
          rules={[
            {
              required: true,
              message: "Por favor ingresa una cantidad",
            },
          ]}
        >
          <InputNumber
            onChange={(e) => setQuantity(e)}
            min={0}
            max={
              dataList.results[payload.key]
                ? parseInt(dataList.results[payload.key].quantity)
                : 5000
            }
            style={{ width: "100%" }}
          />
        </Form.Item>
        <Form.Item
          label="Precio"
          name="price"
          rules={[
            {
              required: true,
              message: "Por favor ingresa un precio",
            },
          ]}
        >
          <InputNumber
            min={0}
            max={100000}
            step={0.1}
            style={{ width: "100%" }}
          />
        </Form.Item>
        {dataList.results[payload.key].quantity === quantity ? (
          <Form.Item name="switch" label="Crear pedido" valuePropName="checked">
            <Switch />
          </Form.Item>
        ) : null}
        <Form.Item>
          <Button type="primary" htmlType="submit" block>
            Aceptar
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};

export default NewSell;
