import React from "react";
import { Form, InputNumber, Button } from "antd";
import { useSelector, useDispatch } from "react-redux";
import { createStock } from "../../store/actions/api";
import { modalClose } from "../../store/actions/modal";

const NewStock = (props) => {
  const payload = useSelector((state) => state.modal.payload);
  const token = useSelector((state) => state.auth.token);
  const dispatch = useDispatch();

  const handleSubmit = (values) => {
    if (payload) {
      const data = { ...values, article: payload };
      console.log("Received values of form: ", values, payload);
      //agregar stock
      dispatch(createStock(data, token));
      dispatch(modalClose());
    }
  };

  React.useEffect(() => {
    if (payload) {
      // get article
    }
  }, []);

  return (
    <>
      <h1>Agregar Stock</h1>
      <Form onFinish={handleSubmit} layout="inline">
        <Form.Item
          label="Cantidad"
          name="quantity"
          rules={[
            {
              required: true,
              message: "Por favor ingresa una cantidad",
            },
          ]}
        >
          <InputNumber min={0} max={100000} />
        </Form.Item>
        <Form.Item
          label="Costo"
          name="cost"
          rules={[
            {
              required: true,
              message: "Por favor ingresa un costo",
            },
          ]}
        >
          <InputNumber min={0} max={100000} step={0.1} />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit">
            Crear
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};
export default NewStock;
